import React, {useState, useEffect} from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Registration from './panels/registration';
import Start from './panels/Start';
import Settings from './panels/Settings';
import Load from './panels/Load';
import Unusedquests from './panels/Unusedquests';
import axios from 'axios';
import config from './panels/config';
let App = (props) => {
	let [activePanel, setActivePanel] = useState('load');
	let [fetchedUser, setFetchedUser] = useState(null);
	// let [fetchedUser, setFetchedUser] = useState({
	// 	id: 124,
	// 	first_name : 'Alexander',
	// 	last_name: 'Zhabovich',
	// 	photo_100: 'https://sun9-3.userapi.com/c639822/v639822055/55abd/Y4nIaWtNYVI.jpg'
	// });
	let [user, setUser] = useState(null);
	let [popout, setPopout] = useState(<ScreenSpinner/>)
	//some us sh
	let [questsApp, setQuestsApp] = useState([]);
	let [unusedQuestsCount, setUnusedQuestsCount] = useState(0);
	useEffect(() => {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					setFetchedUser(e.detail.data)
					console.log('User = ', e.detail.data)
					console.log('UserId = ', e.detail.data.id)
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}, []);
	useEffect(() => {
		if (fetchedUser) axios(`${config.domain}/getUserById/${fetchedUser.id}`).then(response => {
			setPopout(null);
			console.log('response = ', response)
			if (response.data.length == 0) {
				setActivePanel('start');
			} else {
				setUser(response.data[0])
				setActivePanel('home');
			}
		})
	}, [fetchedUser])
	let go = (e) => {
		console.log(e.currentTarget.dataset.to)
		setActivePanel( e.currentTarget.dataset.to );
	};
	let goId = (newId) => {
		setActivePanel(newId);
	}
		return (
			<View activePanel={activePanel} popout={popout}>
				<Home id='home' go={go} fetchedUser={fetchedUser} user={user} setUnusedQuestsCount={setUnusedQuestsCount} setQuestsApp={setQuestsApp}/>
				<Registration id="reg"  go={go} fetchedUser={fetchedUser} setUser={setUser} goId={goId}/>
				<Start id='start' go={go} fetchedUser={fetchedUser}/>
				<Settings id='settings' go={go}/>
				<Load id='load'/>
				<Unusedquests id='unusedquests' questsApp ={questsApp} go={go} unusedQuestsCount={unusedQuestsCount} fetchedUser={fetchedUser} goId={goId}/>
			</View>
		);
}

export default App;
