import React, {useEffect, useState} from 'react';
import {Group, CardGrid, Card,
    ModalPage, ModalPageHeader, ModalRoot} from '@vkontakte/vkui';

import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline'; //DONE
import Icon28CancelOutline from '@vkontakte/icons/dist/28/cancel_outline'; // NOT STARTED
import Icon28RecentOutline from '@vkontakte/icons/dist/28/recent_outline';
let Task = ({quest, popoutType, handleClick}) => {
    // console.log(quest.expirationTime, ' ', Number(Date.now()))
    let [expirationTime, setExpirationTime] = useState(null);
    // useEffect(() => {
    //     setExpirationTime(`${Math.floor((Number(quest.expirationtime) - Number(Date.now())) / ( 1000 * 60 * 60))}:${Math.floor((Number(quest.expirationtime) - Number(Date.now())) / (1000 * 60) % 60)}:${Math.floor((Number(quest.expirationtime) - Number(Date.now())) / (1000) % 60)}`)
    // }, [quest])
    // useEffect(() => {
    //     let g = setInterval(() => {
    //         setExpirationTime(`${Math.floor((Number(quest.expirationtime) - Number(Date.now())) / ( 1000 * 60 * 60))}:${Math.floor((Number(quest.expirationtime) - Number(Date.now())) / (1000 * 60) % 60)}:${Math.floor((Number(quest.expirationtime) - Number(Date.now())) / (1000) % 60)}`)            
    //     }, 100)
    //     return () => clearInterval(g);
    // });
    return (
        <div onClick={() => handleClick(popoutType, quest)} style={{ position: 'relative',}}>
                <div style={{background: 'white', width: '40px', height: '40px', borderRadius: '50%', position: 'absolute',
                    boxShadow: '0 2px 24px 0 rgba(0,0,0,.08), 0 0 2px 0 rgba(0,0,0,.08)',
                    top: 5, left: 15, color: '#2975CC', display: 'flex', justifyContent: 'space-around', alignItems: 'center', zIndex: 3, fontWeight: 'bold'
                        }}>
                        {/* <Icon28CancelOutline/>     */}
                        {quest.completed ? <Icon28DoneOutline/> : <Icon28RecentOutline/>}
                        {/* <span>23:54:10</span> */}
                        {/* 1 */}
                    </div>
                {!quest.completed ? 
                    <div style={{position: 'absolute', top: 17, left: 60, zIndex: 3, fontSize: 16, fontWeight: 'bold', color: '#2975CC'}}>
                            {/* {expirationTime} */}
                    </div>
                    :
                    ""
                }
                <div style={{position: 'absolute', top: 17, right: 25, zIndex: 3, fontWeight: 'bold'}}>
                    <span style={{color: "#2975CC"}}>+{quest.manabonus}</span> / <span style={{color: '#77ba7c'}}>+{quest.expbonus}</span>
                </div>
                <Card size='1' mode='shadow' style={CardStyles}>
                    <div style={{}}>
                         {quest.text}
                    </div>
                </Card>
        </div>
    );
}

let CardStyles = {
    width: 'calc(100% - 40px)', border: 'none', borderRadius: '5px',
    padding: '12px', fontSize: '15px', justifyContent: 'center', alignItems: 'center',
    paddingLeft: '5px',
    marginLeft: '10px',
    height: 70,
    marginBottom: 10,
    display: 'flex',
}


export default Task;


