let rec_thunk = (m, count, user_id, unusedQuestsCount, goId) => {
    if (m.length - 1 < count) {
        axios.post(`${config.domain}/setUser`, {
            newUnusedQuests: unusedQuestsCount - count,
            user_id,
        }).then(response => {
            // alert('nice');
            goId('home');
        })
    } else {
        axios.post(`${config.domain}/addQuestToUser`,{
                ...quests1[m[count]],
                user_id: user_id
            }).then( response => {
            console.log('r = ', response.data)
            rec_thunk(m, count + 1, user_id, unusedQuestsCount, goId);                
        })
    }
}
const Unusedquests = (props) => {
    let [activeQuests, setActiveQuests] = useState(new Set());
    let [count, setCount] = useState(0);
    let [quests, setQuests] = useState(quests1);
    let handleClick = (key) => {
        let aq = activeQuests;
        let b = aq.delete(key);
        if (b) {
            setCount(count - 1)
        } else {
            if (count <= props.unusedQuestsCount - 1) {
                setCount(count + 1);
                aq = aq.add(key);
            }
        }
        setActiveQuests(aq);
    }
    let buttonClick = () => {
        let m = [];
        for (let value of activeQuests) m.push(value);
        rec_thunk(m, 0, props.fetchedUser.id, props.unusedQuestsCount ,props.goId);
    }
    useEffect(() => {
        let newQuest = [...quests];
        for (let i = 0; i < quests.length; i++) {
            newQuest[i].useable = true;
            for (let j = 0; j < props.questsApp.length; j++) {
                if (quests[i].text == props.questsApp[j].text) {
                    newQuest[i].useable = false;
                    break;
                // newQuest[i].useable = newQuest[i].useable && quests[i].text != props.questsApp[j];
                }
                // else 
            }
        }
        setQuests(newQuest);
    }, [props.questsApp])
    return (
        <Panel id={props.id}>
            <PanelHeader
                left={<PanelHeaderButton onClick={props.go} data-to="home">
                    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                </PanelHeaderButton>}
            >
                Выберите квест
            </PanelHeader>
            <Div style={{fontSize: 13, textAlign: 'center'}}>
                <span> У Вас есть <span style={{fontWeight: 'bold', fontSize: 15, color: '#2975CC'}}>{props.unusedQuestsCount - count}</span> неиспользованных квеста(ов)</span>
            </Div>
            <Group>
                {quests.map((e, key) => {
                    if (e.useable) {
                        if (!activeQuests.has(key) && count >= props.unusedQuestsCount) {
                            return <Cell style={{}} onClick={() => handleClick(key)}>{e.text}</Cell>
                        }
                        else {
                                return <Cell selectable style={{}} onClick={() => handleClick(key)}>{e.text}</Cell>
                            }
                    }
                })}
            </Group>
            <Div style={{display:'flex', justifyContent: 'center'}}>
                <Button mode={count > 0 ? `primary` : `secondary`} size='xl' onClick={() => buttonClick()}>Выбрать</Button>
            </Div>
        </Panel>
    );
}

export default Unusedquests;
