import React,{useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import { Panel, List, Button, Group, Div, Avatar, PanelHeader,
		  Epic, Tabbar, TabbarItem, View} from '@vkontakte/vkui';
import axios from 'axios';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon28GraphOutline from '@vkontakte/icons/dist/28/graph_outline';
import Icon28WalletOutline from '@vkontakte/icons/dist/28/wallet_outline';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user_circle_outline';
//components
import Quests from "./MainPages/Quests";
import Profile from "./MainPages/Profile";
import Leader from './MainPages/Leader';
import Shop from './MainPages/Shop';
import config from './config';
const Home = (props) => {
	let [activeStory, setActiveStory] = useState('quest');
	let [popout, setPopout] = useState(null);
	let [user, setUser] = useState(props.user);
	let [needUpdate, setNeedUpdate] = useState(false);
	let onStoryChange = (e) => {
		console.log(e.currentTarget.dataset.story)
		setActiveStory(e.currentTarget.dataset.story);
	}
	useEffect(() => {
		console.log('Effect = ',user)
		console.log(needUpdate);
		if (needUpdate) axios(`${config.domain}/getUserById/${user.id}`).then(response => {
			console.log(response)
			console.log('NEW USER = ',response.data[0])
			setUser(response.data[0]);
			setNeedUpdate(false);
			
		})
	}, [needUpdate])
	return 	<Panel id={props.id} style={{ marginTop: -57}}>
		<Epic activeStory={activeStory} tabbar={
			<Tabbar>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'quest'}
					data-story="quest"
					text="Квесты"
				><Icon28ArticleOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'leaderBoard'}
					data-story="leaderBoard"
					text="Лидеры"
				><Icon28GraphOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'payment'}
					data-story="payment"
					text="Таверна"
				><Icon28MarketOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'profile'}
					data-story="profile"
					text="Профиль"
				><Icon28UserCircleOutline/></TabbarItem>
			</Tabbar>
		}>
			<Quests id='quest'
				fetchedUser = {props.fetchedUser}
				user = {user}
				go = {props.go}
				setNeedUpdate = {setNeedUpdate}
				setUnusedQuestsCount = {props.setUnusedQuestsCount}
				setQuestsApp = {props.setQuestsApp}
			/>
			{/* <View id="quest" activePanel="quest"> */}
			{/* </View> */}
			<Leader 
				id='leaderBoard'
			/>
			<Shop
				id='payment'
			/>
			
			<Profile 
				user = {user}
				id={'profile'}
				fetchedUser = {props.fetchedUser}
				go = {props.go}
			/>
		</Epic>
	</Panel>
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
