import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, PanelHeaderButton, platform, IOS,
Cell, Switch, Group, Header} from '@vkontakte/vkui';
import './Start.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Settings = (props) => {
    console.log(props)
    return (
        <Panel id={props.id}>
            <PanelHeader
                left={<PanelHeaderButton onClick={props.go} data-to="home">
                    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                </PanelHeaderButton>}
            >
                Настройки
            </PanelHeader>
        <Group header={<Header mode="secondary">Общие</Header>}>
            <Cell asideContent={<Switch defaultChecked/>}>
                Уведомления
            </Cell>
        </Group>
        <Group
          header={<Header mode="secondary">Приватность</Header>}
          description="Ваше имя, фамилия, аватарка, уровень в приложении и последние выполненные квесты"
          separator="show"
        >
          <Cell indicator="Доступно для всех" expandable>
            Личные данные
          </Cell>
        </Group>
        </Panel>
    );
}

export default Settings;
