import React, {useState, useEffect} from 'react';
import {Panel, PanelHeader, Group, View, Div,Headline, Cell,
        ActionSheet, ActionSheetItem, Button, Link, Header} from "@vkontakte/vkui";
import Task from './Task';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import axios from 'axios';
import config from './../config';
import bridge from '@vkontakte/vk-bridge';

const SET_QUEST_BUY = 'SET_QUEST_BUY';

let pay_quests = [
    {
        text: "Выпить энергетик",
        description: "Описание",
        manabonus: 100,
        expbonus:  15,
        manadowner: 0,
        expdowner: 0,
    },
]

let Shop = ({id}) => {
    let [popout,setPopout] = useState(null);
    let [payQuests, setPayQuests] = useState(pay_quests);
    //{"app_id": 7416969, }
    useEffect(() => {
        bridge.subscribe((e) => {
            console.log("BRIDGE_EVENT = ", e.detail.type);
            switch (e.detail.type) {
                case "VKWebAppOpenPayFormResult": {
                    alert('payFormResult');
                }
                default:
                    break;
            }
            //VKWebAppOpenPayFormResult
        });
    }, []);
    let handlePopout = (popoutType, quest) => {
        switch (popoutType) {
            case SET_QUEST_BUY: {
                setPopout(<ActionSheet onClose={() => setPopout(null)}>
                            <ActionSheetItem style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center'}}>
                                <span style={{fontWeight: 'bold', fontSize: '20px', color: 'black'}}>{quest.text}</span>
            <span style={{display: 'block', color: 'grey', fontSize: '14px', textAlign: 'center', marginTop: '10px'}}>{quest.description}</span>
                                    {/* <Title level="1" weight="bold" style={{ marginBottom: 16 }}>Title 1 bold</Title> */}
                                </div>
                            </ActionSheetItem>
                             <ActionSheetItem autoclose>
                                <Button  size="xl" onClick={() => {
                                    bridge.send("VKWebAppOpenPayForm", {"app_id": 7416969, "action": "pay-to-user", "params": {"user_id": 477503899, "amount": 1}})
                                }}>Купить</Button>
                            </ActionSheetItem>
                        </ActionSheet>
                )
                break;
            }
            default: {
                alert('shit happens')
                break;
            }
        }
    }
    
    return (
        <View id={id} activePanel={id} popout={popout}>
			<Panel id={id}>
				<PanelHeader>Таверна</PanelHeader>
                <Group header={<Header mode='secondary'>ПЛАТНЫЕ КВЕСТЫ</Header>}>
                {
                        payQuests ? 
                        payQuests.map((quest, key) => {
                            return  (
                                <Task 
                                    key = {key}
                                    handleClick = {handlePopout}
                                    popoutType = {SET_QUEST_BUY}
                                    quest = {quest}
                                />
                            );
                        })
                        :
                        "Loading"
                    }
                    {/* <Cell onClick={ () => handle()}>
                        КУПИТЬ
                    </Cell> */}
                </Group>
			</Panel>
		</View>
    );
}

export default Shop;