import React, {useState, useEffect} from 'react';
import {Panel, PanelHeader, Group, View, Div,Headline, Spinner, 
        ActionSheet, ActionSheetItem, Button, Link} from "@vkontakte/vkui";
import Task from './Task';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import axios from 'axios';
import config from './../config';
import Icon56RecentOutline from '@vkontakte/icons/dist/56/recent_outline';//type_popout
import { PopoutWrapper } from '@vkontakte/vkui';
import Icon56FireOutline from '@vkontakte/icons/dist/56/fire_outline';
const SET_QUEST_DONE = "SET_QUEST_DONE";
const NEW_LVL = 'NEW_LVL';
let Quests = (props) => {
    let [popout,setPopout] = useState(null);
    let [quests, setQuests] = useState(null);
    let [needUpdate, setNeedUpdate] = useState(false);
    useEffect(() => {
        if (needUpdate || quests == null) axios.get(`${config.domain}/getQuestsById/${props.user.id}`).then(response => {
            console.log('quests = ', response.data.quests);
            setQuests(response.data.quests);
            props.setQuestsApp(response.data.quests);
            props.setNeedUpdate(true);
            setNeedUpdate(false);
        })
    }, [needUpdate]);
    let handleClickSend = (quest) => {
        console.log('QUEST ', quest)
        console.log('USER =', props.user);
        if (!quest.completed) {
            axios.post(`${config.domain}/setQuest`, {
                user_id: quest.user_id,
                text: quest.text,
                completed: true,
                user: props.user,
                manaBonus: quest.manabonus,
                expBonus: quest.expbonus
            }).then(response => {
                console.log('HANDLECLICK = ', response)
                if (props.user.level != response.data.newLvl && response.data.newLvl != 0) {
                    handlePopout(NEW_LVL)
                }
                setNeedUpdate(true);
                props.setNeedUpdate(true);
            })
        }
    };
    let handlePopout = (popoutType, quest) => {
        // alert(popoutType)
        switch (popoutType) {
            case SET_QUEST_DONE:
                if (!quest.completed) {
                setPopout(
                    <ActionSheet onClose={() => setPopout(null)}>
                        <ActionSheetItem style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                            <div style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                <Icon56RecentOutline/>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center'}}>
                                <span style={{fontWeight: 'bold', fontSize: '20px', color: 'black'}}>{quest.text}</span>
                <span style={{display: 'block', color: 'grey', fontSize: '14px', textAlign: 'center', marginTop: '10px'}}>{quest.description}</span>
                                {/* <Title level="1" weight="bold" style={{ marginBottom: 16 }}>Title 1 bold</Title> */}
                            </div>
                        </ActionSheetItem>
                         <ActionSheetItem autoclose>
                            <Button  size="xl" onClick={() => handleClickSend(quest) }>Отметить как выполненное</Button>
                        </ActionSheetItem>
                    </ActionSheet>
                    );
                    
                } 
                else {
                    setPopout(
                        <ActionSheet onClose={() => setPopout(null)}>
                            <ActionSheetItem style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                <div style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                    <Icon56CheckCircleOutline/>
                                </div>
                                <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center'}}>
                                    <span style={{fontWeight: 'bold', fontSize: '20px', color: 'black'}}>{quest.text}</span>
                    <span style={{display: 'block', color: 'grey', fontSize: '14px', textAlign: 'center', marginTop: '10px'}}>{quest.description}</span>
                                    {/* <Title level="1" weight="bold" style={{ marginBottom: 16 }}>Title 1 bold</Title> */}
                                </div>
                            </ActionSheetItem>
                             <ActionSheetItem autoclose>
                                <Button  size="xl" mode='secondary'>Выполнено!</Button>
                            </ActionSheetItem>
                        </ActionSheet>
                    );
                }
                break;
            case NEW_LVL: {
                setPopout(
                    <PopoutWrapper alignY="center" alignX="center">
                        <div onClick={() => setPopout(null)} style={{background: 'white', width: 'calc(100% - 132px)', padding: 10, height: 200, borderRadius: 5,
                    boxShadow: '0 8px 8px 0 rgba(0,0,0,.16), 0 0 4px 0 rgba(0,0,0,.08)'}}>
                            <div style={{width: "100%", height: 50, display: 'flex', justifyContent: 'center', alignItems: 'center', fontSize: 18, fontWeight: 'bold'}}>
                                Вы достигли 6 уровня!
                            </div>
                            <div style={{marginTop: 10, color: '#2975CC'}}>
                                <div style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                    <Icon56FireOutline/>
                                </div>
                                <div style={{color: 'black'}}>
                                    <div>Нового:</div>
                                    <div>+2 к максимальному кол-ву квестов</div>
                                </div>
                            </div>
                            <div style={{width: '100%', display: 'flex', justifyContent: 'center'}}>
                                <Button size='l' onClick={() => setPopout(null)} style={{marginTop: 10}}>Круто!</Button>
                            </div>
                        </div>
                    </PopoutWrapper>
                )
                break;
            }
            default:
                alert('some problems with popoutType')
        }
    }
    return (
        <View id="quest" activePanel="quest" popout={popout}>
            <Panel id={props.id}>
                <PanelHeader>Ваши Квесты</PanelHeader>
                {props.user.unusedquests ? 
                <Div style={{fontSize: 13, textAlign: 'center'}}>
                    <Link onClick={(event) => {
                        props.setUnusedQuestsCount(props.user.unusedquests);
                        props.go(event);
                        }} data-to='unusedquests'>У Вас есть <span style={{fontWeight: 'bold', fontSize: 15, color: '#2975CC'}}>{props.user.unusedquests}</span> неиспользованных квеста(ов)</Link>
                </Div>
                :
                ""}
                <Group>
                    {
                        quests ? 
                        quests.map((quest, key) => {
                            return  (
                                <Task 
                                    key = {key}
                                    handleClick = {handlePopout}
                                    popoutType = {SET_QUEST_DONE}
                                    quest = {quest}
                                />
                            );
                        })
                        :
                        <Spinner size='medium' style={{marginTop: 40}}/>
                    }
                    {/* <Task 
                        handleClick = {handlePopout}
                        popoutType = {SET_QUEST_DONE}
                    />
                    <Task 
                        handleClick = {handlePopout}
                        popoutType = {SET_QUEST_DONE}
                    /> */}
                </Group>
            </Panel>
        </View>
    )
}

export default Quests;