import React, {useState} from 'react';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import PropTypes from 'prop-types';
import styles from './registration.module.css';
import { FormLayout, FormLayoutGroup, Input } from '@vkontakte/vkui';
import {Panel, PanelHeader, PanelHeaderButton, platform, IOS, PanelHeaderSimple,
    Avatar, Slider, Radio, Button, Div, CardGrid, Card, Checkbox} from '@vkontakte/vkui';
import axios from 'axios';
import config from './config';
const osname = platform();
const MAX_WEIGHT_USER = 250;
const MAX_HEIGHT_USER = 250;


let Registration = (props) => {
    let [userWeight, setUserWeight] = useState(null);
    let [userHeight, setUserHeight] = useState(null);
    let [activeRadio, setActiveRadio] = useState(1);
    let [activeCheck, setActiveCheck] = useState(-1)
    let handleReg = () => {
        if (!userWeight) {setUserWeight(""); return}
        if (!userHeight) {setUserHeight(""); return}
        if (activeCheck == -1 || activeCheck == 0) {setActiveCheck(0); return}

        console.log(activeCheck)
        axios.post(`${config.domain}/createNewUser`, {
            id: props.fetchedUser.id,
            first_name: props.fetchedUser.first_name,
            last_name: props.fetchedUser.last_name,
            photo_100: props.fetchedUser.photo_100
        }).then(response => {
            props.setUser(response.data);
            axios.post(`${config.domain}/addQuestToUser`, {
                user_id: props.fetchedUser.id,
                text: "Сделать 20 приседаний",
                description: "Описание1",
                manaBonus: 5,
                expBonus: 10,
                manaDowner: -5,
                expDowner: -10
            }).then( response => {
                axios.post(`${config.domain}/addQuestToUser`, {
                    user_id: props.fetchedUser.id,
                    text: "Выпить 1,5 литра воды",
                    description: "Описание2",
                    manaBonus: 15,
                    expBonus: 5,
                    manaDowner: -20,
                    expDowner: -5
                }).then( response => {
                    axios.post(`${config.domain}/addQuestToUser`, {
                        user_id: props.fetchedUser.id,
                        text: "Съесть 1 фрукт",
                        description: "Описание3",
                        manaBonus: 5,
                        expBonus: 5,
                        manaDowner: 0,
                        expDowner: 0
                    }).then( response => {
                        props.goId('home');
                    })
                })
            })
        })
    }
    return (
        <Panel id={props.id}>
            <PanelHeader
                left={<PanelHeaderButton onClick={props.go} data-to="start">
                    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                </PanelHeaderButton>}
            >
                Регистрация
            </PanelHeader>

            <FormLayout style = {{}}>
                <FormLayoutGroup top="Укажите Ваш вес(кг)" style = {{ }} bottom={(userWeight == null || userWeight)  ? "":<div style={{color: '#E64646'}}>Введите свой вес</div>}>
                    <Input 
                        // bottom={(userWeight == null || userWeight)  ? "123": "Введите свой вес!"}
                        status={(userWeight == null || userWeight)  ? "": "error"} 
                        type="number" placeholder="Ваш Вес" value={userWeight == null ? "" : userWeight} onChange={event => setUserWeight(Math.min(Number(event.target.value), MAX_WEIGHT_USER) == 0 ? "" : String(Math.min(Number(event.target.value), MAX_WEIGHT_USER)))}/>
                </FormLayoutGroup>
                <FormLayoutGroup top='Укажите Ваш рост(см)' style = {{ }} bottom={(userHeight == null || userHeight)  ? "":<div style={{color: '#E64646'}}>Введите свой рост</div>}>
                    <Input 
                        status={(userHeight == null || userHeight)  ? "": "error"} 
                        type="number" placeholder="Ваш рост" value={userHeight == null ? "" : userHeight} onChange={event => setUserHeight(Math.min(Number(event.target.value), MAX_HEIGHT_USER) == 0 ? "" : String(Math.min(Number(event.target.value), MAX_HEIGHT_USER)))}/>
                </FormLayoutGroup>
                <FormLayoutGroup top='Укажите Ваш тип телосложения' bottom={'При выборе опирайтесь на Ваш индекс массы тела'}>
                    <div>
                        <Radio name="radio" value="1" description="мало жира, тощие мускулы" defaultChecked onClick={() => setActiveRadio(1)}>Худое</Radio>
                        <Radio name="radio" value="2" description="Средний баланс между жиром и мускулами" onClick={() => setActiveRadio(2)}>Обычное</Radio>
                        <Radio name="radio" value="3" description="Мускулатура, отстутствие жира" onClick={() => setActiveRadio(3)}>Спортивное</Radio>
                        <Radio name="radio" value="4" description="Тело круглое и мягкое, высокий процент жировой ткани" onClick={() => setActiveRadio(4)}>Полное</Radio>
                        {/*<Radio name="radio" value="4">Выше среднего</Radio>*/}
                        {/*<Radio name="radio" value="5">Сильное</Radio>*/}
                    </div>
                </FormLayoutGroup>
                <FormLayoutGroup bottom={activeCheck == 0 ? <div style={{color: '#E64646'}}>Подтвердите обработку</div> : ""}>
                    <Checkbox onClick={() => setActiveCheck(activeCheck == 1 ? 0 : 1)}>Я согласен на обработку персональных данных</Checkbox>
                </FormLayoutGroup>
                <Div>
                    <Button size='xl' stretched style={{}} onClick={() => handleReg()}>Зарегистрироваться</Button>
                </Div>
            </FormLayout>
        </Panel>
    )
};
Registration.propTypes = {
    go: PropTypes.func.isRequired,
    id: PropTypes.string.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        })
    })
};
export default Registration;